import React from "react";
import { Row, Col } from "antd";
import { Link } from "react-scroll";
import heroImg from "../../assets/Hero.png";
import { ReactComponent as Down } from "../../assets/down.svg";

import "./Hero.scss";
import "./HeroMedia.scss";

export default function Hero() {
  return (
    <div className="hero" name="hero">
      <Row>
        <Col span={10}>
          <img alt="hero" src={heroImg} />
        </Col>
        <Col span={10}>
          <div className="hero__info">
            <h1>Sebastián Salamanca</h1>
            <p>
              I´m industrial designer based in Buenos Aires, Argentina. I make
              renders, sketches, 3D models and prototypes. Take a look around
              and don’t hesitate to get in touch if you see anything you like.
            </p>
            <div>
              <Link smooth={true} to="portfolio">
                <Down />
              </Link>
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
}
