import React, { useState } from "react";
import { Menu } from "antd";
import { Link } from "react-router-dom";
import { Link as LinkTo } from "react-scroll";
import logo from "../../assets/S1.png";
import "./MenuTop.scss";
import "./MenuTopMedia.scss";
import MenuResponsive from "../../components/MenuResponsive";

export default function MenuTop() {
  const [open, setOpen] = useState(false);
  const [burger, setBurger] = useState("menu-top__burger");
  const handleBurger = () => {
    setOpen(!open);
    console.log("click");
    burger == "menu-top__burger"
      ? setBurger("menu-top__burger is-open")
      : setBurger("menu-top__burger");
  };

  return (
    <div className="menu-top" name="home">
      <div className="menu-top__logo">
        <img alt="Logo" src={logo} />
      </div>

      <div>
        <div className={burger} onClick={handleBurger}>
          <span></span>
          <span></span>
          <span></span>
        </div>
        {open ? <MenuResponsive handleBurger={handleBurger} /> : null}
        <Menu
          theme="dark"
          mode="horizontal"
          style={{ lineHeight: "64px" }}
          name="home"
        >
          <Menu.Item key="1">
            <Link to="/">Home</Link>
          </Menu.Item>
          <Menu.Item key="2">
            <LinkTo smooth={true} to="portfolio">
              Portfolio
            </LinkTo>
          </Menu.Item>
          <Menu.Item key="3">
            <LinkTo smooth={true} to="contact">
              Contact
            </LinkTo>
          </Menu.Item>
          <Menu.Item key="4">
            <LinkTo smooth={true} to="print">
              3DPrint
            </LinkTo>
          </Menu.Item>
          <Menu.Item key="5">
            <Link to="/shop">Shop</Link>
          </Menu.Item>
        </Menu>
      </div>
    </div>
  );
}
