import React, { useState, useEffect } from "react";
import Pesa from "../../assets/pesa.png";
import Basket1 from "../../assets/b1.jpg";
import Basket2 from "../../assets/b2.jpg";
import Basket3 from "../../assets/b3.jpg";
import "./Print.scss";

import SwiperCore, { Navigation, Pagination, Scrollbar, A11y } from "swiper";

import { Swiper, SwiperSlide } from "swiper/react";

import "swiper/swiper.scss";
import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";
import "swiper/components/scrollbar/scrollbar.scss";

SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);

export default function Print() {
  const [underline, setUnderline] = useState("");

  const handleScroll = () => {
    window.onscroll = () => {
      if (window.pageYOffset > 1599) {
        setUnderline("print__underline");
      } else {
        setUnderline("print__underline-invisible");
      }
    };
  };

  useEffect(() => {
    handleScroll();

    console.log("cambiuooo");
  }, []);
  console.log(window.innerWidth);
  return (
    <div className="print" name="print">
      <h1>
        3D <span>Printing</span>
      </h1>
      <div className={underline}></div>

      <div className="print__info">
        <Swiper
          spaceBetween={50}
          slidesPerView={3}
          navigation
          onSwiper={(swiper) => console.log(swiper)}
          onSlideChange={() => console.log("slide change")}
        >
          <SwiperSlide>
            <div>
              <img alt="FruitBasket1" src={Basket1} />
              <div>
                <p>Fruit Basket</p>
                <a
                  className="gumroad-button"
                  href="https://gumroad.com/l/QNiTqR"
                  data-gumroad-single-product="true"
                >
                  Comprar
                </a>
              </div>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div>
              <img alt="FruitBasket2" src={Basket2} />
              <div>
                <p>Fruit Basket</p>
                <a
                  className="gumroad-button"
                  href="https://gumroad.com/l/QNiTqR"
                  data-gumroad-single-product="true"
                >
                  Comprar
                </a>
              </div>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div>
              <img alt="FruitBasket3" src={Basket3} />
              <div>
                <p>Fruit Basket</p>
                <a
                  className="gumroad-button"
                  href="https://gumroad.com/l/QNiTqR"
                  data-gumroad-single-product="true"
                >
                  Comprar
                </a>
              </div>
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div>
              <img alt="pesa" src={Pesa} />
              <div>
                <p>Russian Weight</p>
                <a
                  className="gumroad-button"
                  href="https://gumroad.com/l/QNiTqR"
                  data-gumroad-single-product="true"
                >
                  Comprar
                </a>
              </div>
            </div>
          </SwiperSlide>
        </Swiper>
      </div>
    </div>
  );
}
