import React, { useState, useEffect } from "react";
import { Link } from "react-scroll";
import { Row } from "antd";
import { ReactComponent as Up } from "../../assets/up.svg";
import Modal from "../../components/Modal";
import ModalResponsive from "../../components/ModalResponsive";

import "./Portfolio.scss";
import "./PortfolioMedia.scss";

export default function Portfolio(props) {
  const [arrowVisible, setArrowVisible] = useState("");
  const [underlineVisible, setUnderlineVisible] = useState("");

  const [initialSlide, setInitialSlide] = useState(1);
  const [display, setDisplay] = useState(false);

  const [modal, setModal] = useState(false);
  const [infoImg, setInfoImg] = useState([]);

  const {
    info: { data, loading },
  } = props;

  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.pageYOffset > 100) {
        setArrowVisible("portfolio__arrow-visible");
        setUnderlineVisible("portfolio__underline");
      } else {
        setArrowVisible("portfolio__arrow-invisible");
        setUnderlineVisible("portfolio__underline-invisible");
      }
    });
  }, []);

  return (
    <div className="portfolio">
      <h1 name="portfolio">Portfolio</h1>
      <div className={underlineVisible}></div>
      <Row className="portfolio__grid">
        {!loading && data
          ? data.map((img, index) => {
              return (
                <ImageRender
                  key={index}
                  img={img}
                  setInitialSlide={setInitialSlide}
                  setDisplay={setDisplay}
                  setModal={setModal}
                  modal={modal}
                  setInfoImg={setInfoImg}
                  infoImg={infoImg}
                />
              );
            })
          : null}
      </Row>
      <Link smooth={true} to="home" className={arrowVisible}>
        <Up />
      </Link>
      {display ? (
        <Modal
          data={data}
          loading={loading}
          passIndex={initialSlide}
          display={display}
          setDisplay={setDisplay}
        />
      ) : null}
    </div>
  );
}

function ImageRender(props) {
  const {
    img,
    setInitialSlide,
    setDisplay,
    setModal,
    setInfoImg,
    infoImg,
    modal,
  } = props;
  return (
    <>
      <div
        onClick={() => {
          setInitialSlide(img.id);
          setDisplay(true);
          setModal(true);
          setInfoImg(img);
        }}
      >
        <img alt={img.title} src={img.image} />
        <div className="portfolio__hover">
          <p>{img.subtitle}</p>
          <p>{img.title}</p>
        </div>
      </div>
      {modal && window.matchMedia(`(max-width: 601px)`).matches ? (
        <ModalResponsive info={infoImg} setModal={setModal} />
      ) : null}
    </>
  );
}
