import React, { useState } from "react";
import { Link as LinkTo } from "react-scroll";
import { ReactComponent as Instagram } from "../../assets/instagram2.svg";
import { ReactComponent as Sobre } from "../../assets/sobre.svg";

import "./MenuResponsive.scss";

export default function MenuResponsive(props) {
  const { handleBurger } = props;

  return (
    <div className="device-menu">
      <ol>
        <li>
          <LinkTo smooth={true} to="home" onClick={() => handleBurger()}>
            Home
          </LinkTo>
        </li>
        <li>
          <LinkTo smooth={true} to="portfolio" onClick={() => handleBurger()}>
            Portfolio
          </LinkTo>
        </li>
        <li>
          <LinkTo smooth={true} to="contact" onClick={() => handleBurger()}>
            Contact
          </LinkTo>
        </li>
        <li>
          <LinkTo smooth={true} to="print" onClick={() => handleBurger()}>
            3DPrint
          </LinkTo>
        </li>
      </ol>
      <div className="device-menu__social">
        <span>
          <a
            href="https://www.instagram.com/sebastiansalamanca.id/"
            target="_blank"
          >
            <Instagram />
          </a>
        </span>
        <span>
          <a target="_blank" href="mailto:estudio@sebastiansalamanca.com">
            <Sobre />
          </a>
        </span>
      </div>
    </div>
  );
}
