import React, { useState, useEffect } from "react";
import Pesa from "../../assets/pesa.png";
import Basket1 from "../../assets/b1.jpg";
import Basket2 from "../../assets/b2.jpg";
import Basket3 from "../../assets/b3.jpg";
import "./PrintResponsive.scss";

export default function PrintResponsive() {
  const [underline, setUnderline] = useState("");

  const handleScroll = () => {
    window.onscroll = () => {
      if (window.pageYOffset > 1599) {
        setUnderline("print__underline");
      } else {
        setUnderline("print__underline-invisible");
      }
    };
  };

  useEffect(() => {
    handleScroll();
  }, []);

  return (
    <div className="print" name="print">
      <h1>
        3D <span>Printing</span>
      </h1>
      <div className={underline}></div>

      <div className="print__info">
        <div>
          <img alt="FruitBasket1" src={Basket1} />
          <div>
            <p>Fruit Basket</p>
            <a
              className="gumroad-button"
              href="https://gumroad.com/l/QNiTqR"
              data-gumroad-single-product="true"
            >
              Comprar
            </a>
          </div>
        </div>

        <div>
          <img alt="FruitBasket2" src={Basket2} />
          <div>
            <p>Fruit Basket</p>
            <a
              className="gumroad-button"
              href="https://gumroad.com/l/QNiTqR"
              data-gumroad-single-product="true"
            >
              Comprar
            </a>
          </div>
        </div>

        <div>
          <img alt="FruitBasket3" src={Basket3} />
          <div>
            <p>Fruit Basket</p>
            <a
              className="gumroad-button"
              href="https://gumroad.com/l/QNiTqR"
              data-gumroad-single-product="true"
            >
              Comprar
            </a>
          </div>
        </div>

        <div>
          <img alt="pesa" src={Pesa} />
          <div>
            <p>Russian Weight</p>
            <a
              className="gumroad-button"
              href="https://gumroad.com/l/QNiTqR"
              data-gumroad-single-product="true"
            >
              Comprar
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
