import React from "react";
import { ReactComponent as ChevronBack } from "../../assets/cl.svg";

import "./ModalResponsive.scss";

export default function ModalResponsive(props) {
  const { info, setModal } = props;
  console.log(info);
  return (
    <div className="modal-responsive">
      <div className="modal-responsive__container">
        <div
          className="modal-responsive__container-svg"
          onClick={() => setModal(false)}
        >
          <ChevronBack />
        </div>
        <div className="modal-responsive__container-title">
          <h3> {info.subtitle}</h3>
          <h2> {info.title}</h2>
          <p>{info.description}</p>

          <div>
            <img alt={info.title} src={info.viewMore[0].image1} />
            {info.viewMore[0].image2 ? (
              <img alt={info.title} src={info.viewMore[0].image2} />
            ) : null}
            {info.viewMore[0].image3 ? (
              <img alt={info.title} src={info.viewMore[0].image3} />
            ) : null}
          </div>
        </div>
      </div>
    </div>
  );
}
