import React, { useState } from "react";
import { ReactComponent as ChevronLeft } from "../../assets/cl.svg";

import "./InfoModal.scss";

export default function InfoModal(props) {
  const { info, setViewMore } = props;

  const [srcMain, setSrcMain] = useState(info.viewMore[0].image1);
  const [srcSecond, setSrcSecond] = useState(info.viewMore[0].image2);
  const [srcThird, setSrcThird] = useState(info.viewMore[0].image3);

  const handleImg = (src) => {
    setSrcSecond(srcMain);
    setSrcMain(src);
  };
  const handleImgO = (src) => {
    setSrcThird(srcMain);
    setSrcMain(src);
  };

  const handleInfo = () => {
    setViewMore(false);
  };
  return (
    <div className="info-modal">
      <div className="info-modal__back" onClick={handleInfo}>
        <ChevronLeft />
      </div>
      <div className="info-modal__grid">
        <div className="info-modal__grid-description">
          <h3>{info.title}</h3>
          <div></div>
          <h4>{info.subtitle}</h4>
          <p>{info.description}</p>
        </div>
        <div className="info-modal__grid-main">
          <img alt={info.title} src={srcMain} />
        </div>
        {info.viewMore[0].image2 ? (
          <img
            alt={info.title}
            src={srcSecond}
            onClick={() => handleImg(srcSecond)}
          />
        ) : null}
        {info.viewMore[0].image3 ? (
          <img
            alt={info.title}
            src={srcThird}
            onClick={() => handleImgO(srcThird)}
          />
        ) : null}
      </div>
    </div>
  );
}
