import React, { useState } from "react";
import SwiperCore, { Navigation, Pagination, Scrollbar, A11y } from "swiper";
import { ReactComponent as ChevronRight } from "../../assets/cr.svg";
import { Swiper, SwiperSlide } from "swiper/react";
import InfoModal from "../../components/InfoModal";

import "./Modal.scss";

// Import Swiper styles
import "swiper/swiper.scss";
import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";
import "swiper/components/scrollbar/scrollbar.scss";

// install Swiper components
SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);

export default function Moddal(props) {
  const { data, loading, passIndex, setDisplay } = props;

  const [viewMore, setViewMore] = useState(false);
  const [info, setInfo] = useState([]);

  const handleInfo = (info) => {
    setInfo(info);
    setViewMore(true);
  };
  console.log(info);

  const handleModal = (e) => {
    if (e.target.classList.value == "close") {
      setDisplay(false);
    } else if (
      e.target.classList.value ==
      "swiper-container swiper-container-initialized swiper-container-horizontal"
    ) {
      setDisplay(false);
    }
  };

  return (
    <div className="moddal" onClick={handleModal}>
      <Swiper
        onSwiper={(swiper) => console.log(swiper)}
        spaceBetween={250}
        slidesPerView={1}
        navigation
        initialSlide={passIndex}
        centeredSlides={true}
        pagination
      >
        {!loading && data
          ? data.map((info, index) => {
              return (
                <SwiperSlide key={index}>
                  <div>
                    <img alt={info.title} src={info.image} />
                  </div>
                  <div>
                    <span className="close">&times;</span>
                    <h2>{info.title}</h2>
                    <div className="underline"></div>
                    <p>{info.description}</p>
                    <div className="btnMore" onClick={() => handleInfo(info)}>
                      <ChevronRight />
                      <p>Ver más</p>
                    </div>
                  </div>
                </SwiperSlide>
              );
            })
          : null}
      </Swiper>
      {viewMore ? <InfoModal info={info} setViewMore={setViewMore} /> : null}
    </div>
  );
}
