import React from "react";
import Logo from "../../assets/S1.png";

import { ReactComponent as Instagram } from "../../assets/instagram.svg";

import "./Footer.scss";

export default function Footer() {
  return (
    <div className="footer" name="contact">
      <div className="footer__info">
        <p>estudio@sebastiansalamanca.com</p>
        <p>tel : +5491156058959</p>
        <p>Buenos Aires, Argentina</p>
        <img alt="logo" src={Logo} />
      </div>
      <div className="footer__info-social">
        <span>
          <a
            rel="search"
            href="https://www.instagram.com/sebastiansalamanca.id/"
            target="_blank"
          >
            <Instagram />
          </a>
        </span>
      </div>
    </div>
  );
}
